import styled from "styled-components";
import fonts from "../../../lib/styles/fonts";
import palette from "../../../lib/styles/palette";
import { SettingContent, SettingLabel } from "../SettingStyles";
import { parseDateString } from "../../../lib/utils";

export type AccountProfilesProp = {
    name: string;
    birth: string;
    phone: string;
    varified: boolean;
    onClick: () => Promise<void>;
};

function AccountProfiles(props: AccountProfilesProp) {
    const { name, birth, phone, varified, onClick } = props;

    const formatBirthDateString = (birth: string) => {
        const dateObj = parseDateString(birth);
        return `${dateObj.year} ${dateObj.month} ${dateObj.date}`;
    };

    return (
        <AccountProfilesBlock>
            <h2></h2>
            <ProfileItem>
                <SettingLabel></SettingLabel>
                <SettingContent>
                    {varified ? name : "   "}
                </SettingContent>
            </ProfileItem>
            <ProfileItem>
                <SettingLabel></SettingLabel>
                <SettingContent>
                    {varified
                        ? formatBirthDateString(birth)
                        : "   "}
                </SettingContent>
            </ProfileItem>
            <ProfileItem>
                <SettingLabel></SettingLabel>
                <SettingContent>
                    {varified ? phone : "   "}
                </SettingContent>
            </ProfileItem>
            {varified ? (
                <CertifiedBlock>  </CertifiedBlock>
            ) : (
                <CertifyBtn onClick={onClick}>  </CertifyBtn>
            )}
        </AccountProfilesBlock>
    );
}

const AccountProfilesBlock = styled.div`
    margin-top: 50px;

    h2 {
        font-size: 14px;
        font-weight: ${fonts.weight.bold};
    }
`;

const ProfileItem = styled.div`
    margin-top: 20px;
`;

const CertifyBtn = styled.button`
    width: 100%;
    font-size: 14px;
    color: ${palette.gray0};
    border: 1px solid ${palette.gray0};
    border-radius: 4px;
    padding: 11px 0;
    margin-top: 20px;
`;

const CertifiedBlock = styled.div`
    display: grid;
    width: 100%;
    font-size: 14px;
    text-align: center;
    color: ${palette.purple0};
    border: 1px solid ${palette.purple0};
    border-radius: 4px;
    padding: 12px 0;
    margin-top: 20px;
`;

export default AccountProfiles;
