import styled from "styled-components";
import fonts from "../../../lib/styles/fonts";
import media from "../../../lib/styles/media";
import palette from "../../../lib/styles/palette";
import { formatPaymentMethod, formatPrice } from "../../../lib/utils";
import { UnderLine } from "../../../styles/Texts";
import {
    parseDateString,
    formatTime,
    copyToClipboard,
} from "../../../lib/utils";

import { ReactComponent as CopyIco } from "../../../static/icons/global/ico_copy.svg";

export type ResultDetailsProp = {
    priceAmount: number;
    paymentMethod: string;
    scheduledPaymentDate: string;
    scheduledDeliveryDate?: string;
};

function ResultDetails(props: ResultDetailsProp) {
    const {
        priceAmount,
        paymentMethod,
        scheduledPaymentDate,
        scheduledDeliveryDate,
    } = props;

    const deadLineDateText = (() => {
        const todayVal = new Date().getTime();
        const aDateVal = 1000 * 60 * 60 * 24;
        const deadLineDateVal = todayVal + aDateVal;
        const deadLineDateStr = new Date(deadLineDateVal).toString();
        const { month, date, day } = parseDateString(deadLineDateStr);
        return `${month} ${date}(${day}) ${formatTime(deadLineDateStr)}`;
    })();
    const scheduledPaymentDateText = (() => {
        const { year, month, date } = parseDateString(scheduledPaymentDate);
        return `${year} ${month} ${date}`;
    })();

    const onCopyBtnClick = () => {
        const bankAccountText =
            "301-0299-1930-51 (,  )";
        copyToClipboard(bankAccountText, "  .");
    };

    return (
        <Block>
            <h3> </h3>
            <MainContainer>
                <Details>
                    <DetailRow>
                        <label> </label>
                        <p className="amount">
                             {formatPrice(priceAmount)}
                        </p>
                    </DetailRow>
                    <DetailRow>
                        <label> </label>
                        <p className="method">
                            {formatPaymentMethod(paymentMethod)}
                        </p>
                    </DetailRow>
                    <DetailRow>
                        <label> </label>
                        <p className="due-date">{scheduledPaymentDateText}</p>
                    </DetailRow>
                    {/*         */}
                    {/* {scheduledDeliveryDate && (
                        <DetailRow>
                            <label> </label>
                            <p className="due-date">
                                {scheduledDeliveryDateText}
                            </p>
                        </DetailRow>
                    )} */}
                </Details>
                <Notice>
                    {formatPaymentMethod(paymentMethod) !== " " ? (
                        <NoticeRow>
                                  
                              .
                        </NoticeRow>
                    ) : priceAmount === 0 ? (
                        <>
                            <NoticeRow>
                                  <span>  </span>
                                ,      
                                 .
                            </NoticeRow>
                            <NoticeRow>
                                   {" "}
                                <span>
                                         
                                    .
                                </span>{" "}
                            </NoticeRow>
                        </>
                    ) : (
                        <>
                            <NoticeRow>
                                      
                                   .
                                <br />
                                <div className="account-num">
                                    <span>
                                         301-0299-1930-51 (
                                        )
                                    </span>
                                    <CopyBtn onClick={onCopyBtnClick}>
                                        <StyledCopyIco />
                                        
                                    </CopyBtn>
                                </div>
                            </NoticeRow>
                            <NoticeRow>
                                 {" "}
                                <UnderLine>{deadLineDateText}</UnderLine>
                                 .
                            </NoticeRow>
                            <NoticeRow>
                                     
                                .
                            </NoticeRow>
                            <NoticeRow>
                                     
                                    
                                .
                            </NoticeRow>
                            <NoticeRow>
                                      
                                 ,    .
                            </NoticeRow>
                        </>
                    )}
                </Notice>
            </MainContainer>
        </Block>
    );
}

const Block = styled.div`
    margin-top: 100px;

    h3 {
        ${fonts.size.scale22}
        font-weight: ${fonts.weight.bold};
    }

    ${media.mobile} {
        margin-top: 60px;

        h3 {
            padding: 0 20px;
        }
    }
`;

const MainContainer = styled.div`
    border: 1px solid ${palette.gray3};
    border-radius: 4px;
    padding: 0 40px;
    margin-top: 28px;

    ${media.mobile} {
        border-right: none;
        border-left: none;
        border-radius: 0;
        padding: 0;
        margin-top: 16px;
    }
`;

const Details = styled.div`
    padding: 40px 0 32px;

    ${media.mobile} {
        padding: 24px 20px;
    }
`;

const DetailRow = styled.div`
    ${fonts.size.scale18}
    display: grid;
    grid-template-columns: 126px 1fr;

    & + & {
        margin-top: 28px;
    }

    label {
        font-weight: ${fonts.weight.bold};
    }

    p {
        color: ${palette.gray0};
    }

    p.amount {
        color: ${palette.purple0};
        font-weight: ${fonts.weight.bold};
    }

    ${media.mobile} {
        grid-template-columns: 1fr;

        & + & {
            margin-top: 20px;
        }

        p {
            margin-top: 8px;
        }
    }
`;

const Notice = styled.ul`
    border-top: 1px solid ${palette.gray3};
    padding: 28px 0;

    ${media.mobile} {
        background-color: ${palette.white2};
        border-top: none;
        padding: 16px 20px;
    }
`;

const NoticeRow = styled.li`
    ${fonts.size.scale16}
    ${fonts.lineHeight.scale18}
    position: relative;
    color: ${palette.gray0};
    padding-left: 12px;

    & + & {
        margin-top: 16px;
    }

    &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 8px;
        display: inline-block;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background-color: ${palette.gray0};
    }

    .account-num {
        display: inline-flex;
        align-items: center;
    }

    span {
        font-weight: ${fonts.weight.bold};
        word-break: keep-all;
    }
`;

const CopyBtn = styled.button`
    ${fonts.size.scale16}
    ${fonts.lineHeight.scale16}
    display: inline-flex;
    align-items: center;
    font-weight: ${fonts.weight.bold};
    color: ${palette.purple0};
    padding: 0;
    margin-left: 10px;

    ${media.mobile} {
        width: 60px;
    }
`;

const StyledCopyIco = styled(CopyIco)`
    width: 18px;
    height: 18px;
    margin: -3px 4px 0 0;

    .ico-copy-path {
        stroke: ${palette.purple0};
    }

    ${media.mobile} {
        width: 14px;
        height: 14px;
        margin: -2px 2px 0 0;
    }
`;

export default ResultDetails;
